import {Component,EventEmitter, OnInit,Output, Input } from '@angular/core';
import { TaskItem } from '../../models/taskitem';
import { TaskItemUpdate } from '../../models/task-item-update';
import { TaskitemserviceService } from '../../services/taskitemservice.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-task-model',
  templateUrl: './task-model.component.html',
  styleUrls: ['./task-model.component.css']
})
export class TaskModelComponent implements OnInit {
 @Input() selectedTaskItem:TaskItem;
 @Output() updatedTaskItems=new EventEmitter<boolean>()
 taskUpdateForm: FormGroup;
 
  constructor(private taskService: TaskitemserviceService, public activeModal: NgbActiveModal, private formBuilder: FormBuilder) 
  {
  }

  ngOnInit() {
    }

  updateTaskItem() {
    console.log(this.selectedTaskItem);

   if (this.selectedTaskItem) {
      //This is the TaskItem to be updated
      var taskItemUpdate = new TaskItemUpdate();
      taskItemUpdate.name = this.selectedTaskItem.name;
      taskItemUpdate.description = this.selectedTaskItem.description;
      taskItemUpdate.status = this.selectedTaskItem.status;
      let selectedTaskId = this.selectedTaskItem.taskId;

      this.taskService.updateTaskItem(taskItemUpdate, selectedTaskId).subscribe(
        res => {
          console.log(res);
          this.updatedTaskItems.emit(true);
        }
      );
      //Unset the Selected task Item
      this.selectedTaskItem = null;
    }
  }

  closeModal() {
    this.activeModal.close('Modal Closed');
  }
  

}
