import { Component, OnInit } from '@angular/core';
import { TaskItem } from '../models/taskitem';
import { TaskItemCreate } from '../models/task-item-create';
import { TaskitemserviceService } from '../services/taskitemservice.service';
import { Subscription } from 'rxjs';
import {FormGroup, FormControl, Validators, FormBuilder} from '@angular/forms'
import { TaskModelComponent } from '../shared/task-model/task-model.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-task-list',
  templateUrl: './task-list.component.html',
  styleUrls: ['./task-list.component.css']
})
export class TaskListComponent implements OnInit {

  taskItems: TaskItem[];
  statusIcon: string;
  taskItemsSubscription: Subscription;
  errorMessage: any;
  newTaskItem: TaskItemCreate = new TaskItemCreate();
  selectedTaskItem: TaskItem;


  constructor(private taskService: TaskitemserviceService, private modalService: NgbModal, private formBuilder: FormBuilder) { }

  ngOnInit() {
    this.getTaskItems();
  }

  reloadTaskItems() {
    this.getTaskItems();
  }

  getTaskItems() {
    if (this.taskItemsSubscription) {
      this.taskItemsSubscription.unsubscribe();
    }
    this.taskItemsSubscription = this.taskService.getTaskItems().subscribe(
      taskItems => {
        this.taskItems = taskItems;
      },
      error => this.errorMessage = <any>error
    );
  }

  createTaskItem() {
    if (this.newTaskItem.status == null)
      this.newTaskItem.status = "NotStarted";

    this.taskService.createTaskItem(this.newTaskItem).subscribe(
      taskItem => {
        console.log(taskItem);
        this.taskItems.push(taskItem);
        this.newTaskItem = new TaskItemCreate();
      },
      error => {
        this.errorMessage = <any>error;
        this.newTaskItem = new TaskItemCreate();
      }
    );
  }

  openTaskModel(taskItem:TaskItem) {
    const modalRef = this.modalService.open(TaskModelComponent);
    modalRef.componentInstance.selectedTaskItem = taskItem;
    
    modalRef.result.then((result) => {
      console.log(result);
    }).catch((error) => {
      console.log(error);
    });
  }
}
